import { Component, OnInit } from '@angular/core';
import { KeyValue } from '@angular/common';
import { UserService } from 'src/app/services/user.service';
import { UtilsService } from 'src/app/services/utils.service';

@Component({
  selector: 'app-admin-admins',
  templateUrl: './admin-admins.component.html',
  styleUrls: ['./admin-admins.component.css']
})
export class AdminAdminsComponent implements OnInit {
  public pageSize: number = 20;
  public pageNumber: number = 0;
  public totalPages: number = 0;
  public loading: boolean = false;
  public filter: string | null = null;
  public categoriesHeading: string[] = [];
  public list: any[] = [
    { 'Nombre del usuario': '', 'Nombre': '', 'Correo electrónico': '', 'Rol de usuario': '' }
  ];

  isShowDiv: boolean = true;

  //usados en el modal//
  showFilterModal: boolean = false;
  appliedFilters: { name: string, value: any }[] = [];
  codigos: any;
  nit: any;

  constructor(
    private userService: UserService,
    private utilsService: UtilsService
  ) {
    this.categoriesHeading = Object.keys(this.list[0]);
  }

  async ngOnInit(): Promise<void> {
    this.loading = true;
    this.isShowDiv = true;

    const token: string | undefined = sessionStorage.getItem('Token')?.toString();
    if (token) {
      this.loadData(this.pageNumber, this.pageSize, this.filter, false);
    }
    else {
      this.utilsService.dialog('ERROR', 'Error', 'No tienes acceso.');
      this.utilsService.goToPage('/login');
    }
    this.loading = false;
  }

  originalOrder = (a: KeyValue<string, string>, b: KeyValue<string, string>): number => {
    return 0;
  }

  onLeft() {
    if (this.pageNumber + 1 != 1) {
      this.pageNumber--;
      this.list.length = 0;
      this.loadData(this.pageNumber, this.pageSize, this.filter, false);
    }
  }

  onRight() {
    if (this.pageNumber + 1 != this.totalPages) {
      this.pageNumber++;
      this.list.length = 0;
      this.loadData(this.pageNumber, this.pageSize, this.filter, false);
    }
  }

  goToPage(pageName: string, params?: {}) {
    this.utilsService.goToPage(pageName, params);
  }

  showActions(event: any, index: number) {
    this.isShowDiv = true;
    const element = document.getElementById('item' + index);
    const listItes = document.getElementsByClassName('actions-disble')[0];
    if (listItes) {
      listItes.classList.remove('actions-disble');
    }
    if (('item' + index) !== listItes?.id || !listItes.id) {
      element?.classList.add('actions-disble');
    }
  }

  async searchReturn() {
    if (this.filter!.length === 0) {
      this.filter = null;
      this.totalPages = 0;
      this.pageNumber = 0;
      await this.loadData(this.pageNumber, this.pageSize, null, false);
    }
  }

  async loadData2(page: number | null, size: number | null, filter: string | null, search?: boolean) {
    this.loading = true;
    return this.userService.getAllUsersAdmin(this.pageNumber, this.pageSize, 'admins').then(
      response => {
        // console.log(response.result)
        if (response.success === true) {
          this.list = response.result.map((e: any) => {
            return {
              id: e?.id,
              'Nombre del usuario': e?.username,
              'Nombre': `${e?.firstName} ${e?.secondName} ${e?.firstLastname} ${e?.secondLastname}`,
              'Correo electrónico': e.participant?.email,
              'Rol de usuario': e?.role ? (e.role.slice(5, e.role.length)).replace('_', ' ') : ''
            }
          });
          this.totalPages = Math.ceil(response.count / this.pageSize);
        } else {
          console.log('error 1', response);
          this.utilsService.dialog('ERROR', 'Error', '¡Error al consultar los usuarios!');
        }
      },
      error => {
        console.log(error);
        this.utilsService.dialog('ERROR', 'Error', 'Error al cargar usuarios!');
      }
    ).finally(() => {
      this.loading = false;
    });
  }

  async loadData(page: number | null, size: number | null, filter: string | null, search?: boolean) {
    this.loading = true;
    let response: any;

    if (this.filter != null && this.filter != '') {
      (search) && (this.pageNumber = 0);
      response = await this.userService.searchAllUsers(this.pageNumber, this.pageSize, 'admins', this.filter);
    } else {
      // return this.userService.getAllUsersAdmin(this.pageNumber, this.pageSize, 'admins');
      // response = await this.userService.getAllUsers(this.pageNumber, this.pageSize, null);
      response = await this.userService.getAllUsersAdmin(this.pageNumber, this.pageSize, 'admins');
    }

    if (response.success && response.success == true) {
      this.list = response.result.map((e: any) => {
        return {
          // id: e?.id,
          // 'Nombre del usuario': `${e?.firstName} ${e?.secondName} ${e?.firstLastname} ${e?.secondLastname}`,
          // 'Codigo': e?.codigo,
          // 'Puesto': e?.puesto?.name,
          // 'NIT': e?.personalNIT,
          // 'Teléfono': e?.phone,
          // 'Correo electrónico': e?.email,

          id: e?.id,
          'Nombre del usuario': e?.username,
          'Nombre': `${e?.firstName} ${e?.secondName} ${e?.firstLastname} ${e?.secondLastname}`,
          'Correo electrónico': e.participant?.email,
          'Rol de usuario': e?.role ? (e.role.slice(5, e.role.length)).replace('_', ' ') : ''
        }
      });
      this.totalPages = Math.ceil(response.count / this.pageSize);
    } else {
      this.utilsService.dialog('ERROR', 'Error', response?.error?.message?.description || '¡Error al consultar usuarios!');
    }
    this.loading = false;
  }

  deletedUser(user_id: number) {
    this.loading = true;

    const userID = sessionStorage.getItem('UserID')?.toString();
    const userNAME = sessionStorage.getItem('Name')?.toString();
    const userEMAIL = sessionStorage.getItem('Email')?.toString();
    const data = {
      'reason_delete': 'Solicitud para eliminar usuario',
      'reason_delete_description': `El usuario: ${userNAME} con id ${userID} solicito eliminar al usuario con id ${user_id}, contactalo a ${userEMAIL}`
    };

    this.userService.deletedUser(user_id, data).then(
      async (response) => {
        console.log(response);
        this.loadData(this.pageNumber, this.pageSize, this.filter, false);
        this.utilsService.dialog('SUCCESS', 'Éxito', '¡Usuario eliminado!');
      },
      error => {
        console.log(error);
        this.utilsService.dialog('ERROR', 'Error', '¡El usuario no se elimino!');
      }).finally(() => {
        this.loading = false
      })
  }

  //TS para el modal hijo

// Método para mostrar el modal de filtros
openFilterModal() {
  //console.log('openFilterModal() mostrar');
  this.showFilterModal = true;
}

// Método para limpiar los filtros
clearFilters() {
  // Lógica para limpiar los filtros
  // Limpiar los campos de filtro
  this.codigos = '';
  this.nit = '';
  // Limpiar otros campos de filtro...

  // Limpiar la lista de filtros aplicados
  this.appliedFilters = [];
 
}

// Método para realizar la búsqueda con los filtros seleccionados
search() {
  // Lógica para realizar la búsqueda

  //lógica para aplicar filtros
  if (this.codigos) {
    this.appliedFilters.push({ name: 'Código', value: this.codigos });
  }
  if (this.nit) {
    this.appliedFilters.push({ name: 'NIT', value: this.nit });
  }
  // Repite para otros filtros...

  // Limpiar los campos de filtro después de agregar los filtros aplicados
  this.clearFilters();
  this.showFilterModal = false;
}

closeFilterModal() {
  this.showFilterModal = false; // Cambia el valor a false para ocultar el modal
}

removeFilter(index: number) {
  this.appliedFilters.splice(index, 1);
}
}
